.yellow-line{
  width: 100%;
  float: left;
  display: block;
  height: 37px;
  background-color: #ffd800;
}
.support-search{
  width: 100%;
  float: left;
  display: block;
  position: relative;
}
.support_block {
    width: 20%;
    height: 37px;
    padding: 0;
    background-color: #334a51;
    font-size: 11px;
    display: block;
    float: left;
    position: relative;
    &-title{
      text-align: center;
      font-family: $ob;
      font-size: 14px;
      color: #fff;
      line-height: 0;
    }
}
.search-panel{
  width: 80%;
  float: left;
  display: block;
  position: relative;
}

.help-page__content{
  width: 100%;
  float: left;
  display: block;
  position: relative;
  text-align: center;
  &_help-category{
    width: 20%;
    float: left;
    display: block;
    position: relative;
    height: 200px;

    .minus, .sub-menu {
        display: none;
      }

  }
  &_all{
    float: left;
    width: 80%;
    display: block;
    height: auto;
    border-left: 1px solid #dedede;
    box-sizing: border-box;
    .breadcrumbs {
    	float: left;
    	width: 100%;
    	position: relative;
    	&__list {
    		list-style-type: none;
    		float: left;
    		margin: 15px 0 15px 30px;
    		padding: 0;
    		li {
    			display: inline-block;
    			margin: 0 10px;
    			position: relative;
    			font-size: 12px;
    			font-family:$or ;
    			a {
    				color: #334a51;
    			}
    			&:first-of-type {
    				margin-left: 0;
    			}
    			// &:before {
    			// 	content: "";
    			// 	position: absolute;
    			// 	bottom: -10px;
    			// 	left: 0;
    			// 	width: 100%;
    			// 	height: 0;
    			// 	border-bottom: 2px dashed #ff6000;
    			// }
    			&:after {
    				content: "/";
    				position: absolute;
    				right: -18px;
    				bottom: 0;
    			}
    			&:last-of-type {
    				line-height: 30px;
    				a{
    					float: right;
    					line-height: 30px;
    					font-size: 12px;
    		      color: #334a51;
    		      font-family:$or ;
    			    padding-left: 20px;
    					box-sizing: border-box;
    				}
    				&:after, &:before {
    					content: none;
    				}
    			}
    		}
    	}
    }
    &-help-answer{
      width: 100%;
      float: left;
      position: relative;
      text-align: center;
      .help-answer_title{
        font-family: $or;
        font-size: 16px;
        color: #5c6c66;
        text-align: left;
        line-height: 37px;
        background-color: #fff4b8;
        padding-left: 70px;
        box-sizing: border-box;
      }
      .help-answer_item{
        font-family: $ob;
        font-size: 12px;
        padding:10px 0  10px 70px;
        box-sizing: border-box;
        width: 80%;
        float: left;
        text-align: left;
        color: #334a51;

      }
    }
  }
}
