.delivery_block{
width: 20%;
padding:  0;
background-color: #334a51;
font-size: 11px;
display: block;
float: left;
position: relative;
}
.delivery_block .delivery_list {
padding: 0px 6px 3px 6px;
height:35px;
cursor: pointer;
margin: 0 20px 0 0;
color: #ed4226;
background-color: #334a51;
width: 100%;
z-index: 11;

}
.delivery_list #btn {
		width: 0;
		height: 0;
  	border-left: 7px solid transparent;
  	border-right: 7px solid transparent;
  	border-top: 12px solid #ffd800;
		float: right;
		position: absolute;
		right: 5%;
		top: 10px;

}
.delivery_list span {
padding: 2px 0 0 20px;
display: block;
font-weight: bold;
font-size: 16px;
font-family: $osb;
color: #fff;
height: 35px;
line-height: 30px;
}
.delivery_block ul.cities_list {
margin-top: 0px;
z-index: 10;
background-color: #fff6c4;
position: absolute;
width: 100%;
padding: 0;

display: none;
}
ul.cities_list li {
padding: 6px 0px 6px 30px;
list-style-type: none;
font-family: $or;
font-size: 16px;
color: #334a51;
position: relative;
&:before{
	content: "";
	position: absolute;
	background-image: url("../img/disk.png");
	width: 5px;
	height: 5px;
	margin-left: -20px;
  margin-top: 10px;

}

}
ul.cities_list li:hover {
cursor: pointer;
background: #fff;
}
.delivery_block .delivery_text {
padding: 3px 5px;
margin: 5px 0 0 3px;
}






