.ad-concrete-header{
  width: 100%;
  float: left;
}
.ad-concrete__header{
  width: 100%;
  float: left;
  position: relative;
  padding: 10px 0 20px;
  box-sizing: border-box;
  border-top: 1px solid #dedede;
  border-bottom: 1px solid #dedede;
  margin: 10px 0;
  .average-ad-time{
    float: left;
    width: 15%;
  }
}

.add-ad-time-icon{
  display: inline-block;
  float: left;
  background-image: url("/img/add-ad-time-icon.png");
  background-repeat: no-repeat;
  background-size: cover;
  size: 11px;
  margin-right: 5px;
  margin-top: 3px;
}

.edit-ad{
  float: left;
  min-width: 60%;
  text-decoration: underline;
  font-family: $or;
  color: #334a51;
  font-size: 12px;
  .edit-ad-icon{
    display: inline-block;
    float: left;
    background-image: url("/img/edit-ad-icon.png");
    size: 13px;
    background-repeat: no-repeat;
    background-size: cover;
    margin-right: 5px;
    margin-top: 2px;
    cursor: pointer;
  }
  @include hov-yellow;
}

.ad-concrete__header_title{
  text-align: left;
  width: 100%;
  font-size: 30px;
  font-family: $oeb;
  color: #000;
  padding: 0;
  margin-bottom: 0;
}

.ad-concrete__content{
  width: 100%;
  float: left;
}

.ad-concrete__content_left{
  float: left;
  width: 75%;
  position: relative;
  .org{
    width: 100%;
    .org-items {
      float: left;
      width: 18.3%;
      margin-left: 18px;
      &:first-of-type{
        margin-left: 0;
      }
    }
  }
}

.ad-concrete__content_right{
  float: left;
  width: 25%;
  position: relative;
  text-align: center;
  p{
    width: 100%;
    text-align: center;
    font-size: 12px;
    color: #9a9a9a;
    font-family: $or;
  }
}

.speed-trade{
  width: 85%;
  float: right;
  background-color: #ffd800;
  border-top-left-radius: 30px;
  border-bottom-left-radius: 30px;
  padding: 20px 0;
  box-sizing: border-box;
  h2{
    font-size: 14px;
    font-family: $ob;
    color: #000;
    margin: 0;
    padding-bottom: 10px;
    box-sizing: border-box;
  }
  a{
    width: 96%;
    float: right;
    text-decoration: none;
    font-size: 16px;
    font-family: $osb;
    margin: 5px 0;
    color: #334a51;
    text-align: left;
  }
  .crown-icon{
    background-image: url("/img/crown-icon.png");
    display: inline-block;
    size: 20px 14px;
    background-repeat: no-repeat;
    margin-right: 5px;
  }
  .map-in{
    background-image: url("/img/map-in.png");
    size: 17px;
    display: inline-block;
    background-repeat: no-repeat;
    margin-right: 5px;
  }
  .line-chart{
    size: 18px 16px;
    background-image: url("/img/line-chart.png");
    display: inline-block;
    background-repeat: no-repeat;
    margin-right: 5px;
  }
}

.banner-1, .banner-2{
  height: 400px;
  width: 240px;
  background-color: #e5e5e5;
  float: none;
  margin: 20px auto;
  display: inline-block;
}
.slider-for{
	width: 89%;
  float: left;
	height: 370px;
	overflow: hidden;
  cursor: pointer;
  .slick-arrow{
  		position: absolute;
      top: 50%;
      z-index: 50;
      margin-top: -12px;
  }
  .slick-prev{
  	left: 0;

  }
  .slick-next{
  	right: 0;
  }
}
img{
	width: 100%;
	min-height: 100%;
}
.slider-nav{
	width: 10%;
	height: 370px;
	margin-left: 5px;
  float: left;
  display: inline-block;
  cursor: pointer;
}
.slider-nav .slick-track{
	height: 85px;
}

.about-seller{
  width: 100%;
  float: left;
  padding: 20px 0;
  box-sizing: border-box;
  border-top: 1px solid #dedede;
  border-bottom: 1px solid #dedede;
  margin-top: 20px;
}

.user{
  width: 20%;
  float: left;
  position: relative;
  .thumb{
    width: 30%;
    float: left;
    overflow: hidden;
    border-radius: 50%;
    height: 35px;
    margin-right: 5px;
    img{
      width: auto;
      height: 100%;
    }
  }
  span{
    font-family: $or;
    color: #878787;
    font-size: 12px;
    text-align: left;
  }
  h4{
    margin: 0;
    font-size: 16px;
    font-family: $ob;
    color: #000;
    text-align: left;
  }
}
.user-city, .user-number, .user-mail{
  float: left;
  display: inline-block;
  width: 19%;
  font-size: 12px;
  font-family: $ob;
  color: #9b8615;
  text-decoration: none;
  padding: 5px 0;
  box-sizing: border-box;
  margin: 0 5px;
  .mail{
    size: 14px 9px;
    background-image: url("/img/mail.png");
    display: inline-block;
    float: left;
    background-repeat: no-repeat;
    margin-top: 3px;
    margin-right: 3px;
  }
}
.geo-icon{
  size: 9px 11px;
  background-image: url("/img/green-geo.png");
  display: inline-block;
  float: left;
  background-repeat: no-repeat;
  margin-top: 3px;
  margin-right: 3px;
}

.user-city{
  color: #878787;
  font-family: $or;
  a{
    font-size: 12px;
    font-family: $ob;
    color: #9b8615;
    text-decoration: none;
  }
}

.ad-price{
  width: 17%;
  float: right;
  position: relative;
  .price{
    font-size: 30px;
    color: #000;
    font-family: $ob;
  }
  .rubl-icon {
    display: inline-block;
    height: 19px;
    width: 20px;
    top: -7px;
    margin-left: 2px;
    svg{
      height: 19px;
    width: 18px;
    }
  }
}

.ad-info{
  width: 60%;
  float: left;
  padding: 20px 0;
  box-sizing: border-box;
  .ad-info-row{
    width: 100%;
    float: left;
    display: inline-block;
    padding: 8px 0 8px 10px;
    box-sizing: border-box;
    span{
      font-family: $ob;
      font-size: 14px;
      width: 50%;
      float: left;
      text-align: left;
    }
    p{
      font-family: $or;
      font-size: 14px;
      width: 50%;
      float: left;
      text-align: left;
      color: #9a988b;
      margin: 0;
    }
  }
  .ad-info-row:nth-of-type(odd){
    background-color: #fff1a3;
  }
  .brief-information{
    width: 100%;
    float: left;
    color: #9a9a9a;
    text-align: left;
    font-size: 15px;
    font-family: $or;
  }
}

.user-ad-info{
  width: 40%;
  float: left;
  position: relative;
  padding-left: 80px;
  box-sizing: border-box;
  padding-top: 50px;
  span{
    display: inline-block;
    font-size: 16px;
    font-family: $ob;
    color: #000;
    margin: 0 0 50px;
    a{
      font-size: 16px;
      padding-left: 10px;
      box-sizing: border-box;
    }
  }
  a{
    color: #9b8615;
    font-family: $ob;
    text-transform: lowercase;
  }
  .user-ad-info-pic{
    width: 100%;
    position: relative;
    display: inline-block;
    margin: 20px 0;
    img{
      height: 120px;
      width: auto;
    }
    p{
      display: inline-block;
      text-transform: capitalize;
      font-size: 16px;
      font-family: $or;
      color: #000;
      margin: 0;
      padding: 10px 0;
      box-sizing: border-box;
    }
  }
  .number-ad{
    width: 100%;
    float: left;
    text-align: right;
    color: #9a9a9a;
    font-family: $or;
    font-size: 12px;
    span{
      font-family: $oeb;
      color: #9a9a9a;
    }
  }
}

.share-ad{
  width: 100%;
  float: left;
  display: inline-block;
  padding: 10px 0;
  box-sizing: border-box;
  border-top-right-radius: 30px;
  border-bottom-right-radius: 30px;
  border-top: 1px solid #dedede;
  border-bottom: 1px solid #dedede;
  margin: 10px 0;
}

.mail-1{
  background-image: url("/img/mail-1.png");
  size: 20px 13px;
  display: inline-block;
  float: left;
  background-repeat: no-repeat;
  margin-top: 3px;
  margin-right: 3px;
  position: relative;
  z-index: 2;
}

.favorite-icon{
  background-image: url("/img/favorite-icon.png");
  size: 20px;
  display: inline-block;
  float: left;
  background-repeat: no-repeat;

  margin-right: 3px;
  position: relative;
  z-index: 2;
}

.coplain-icon{
  background-image: url("/img/coplain.png");
  size: 20px;
  display: inline-block;
  float: left;
  background-repeat: no-repeat;
  margin-right: 3px;
  position: relative;
  z-index: 2;
}
.share-icon{
  background-image: url("/img/share-icon.png");
  size: 20px;
  display: inline-block;
  float: left;
  background-repeat: no-repeat;
  margin-right: 3px;
  position: relative;
  z-index: 2;
}

.write-seller, .favorite-seller, .coplain-seller, .share-seller{
  min-width: 17%;
  float: left;
  display: inline-block;
  margin: 0 10px;
  font-family: $osb;
  font-size: 12px;
  color: #000;
  text-decoration: none;
  line-height: 22px;
  &:first-of-type{
    margin-left: 0;
  }
}
.mini-social{
  min-width: 15%;
  float: right;
  display: inline-block;
  .mini-social-icon{
    text-decoration: none;
    size: 16px;
    display: inline-block;
    float: left;
    background-repeat: no-repeat;
    margin: 0 2px;
  }
  .mini-social-vk{
    background-image: url("/img/mini-social-vk.png");
  }
  .mini-social-ok{
    background-image: url("/img/mini-social-ok.png");
  }
  .mini-social-fb{
    background-image: url("/img/mini-social-fb.png");
  }
  .mini-social-gp{
    background-image: url("/img/mini-social-gp.png");
  }
  .mini-social-twi{
    background-image: url("/img/mini-social-twi.png");
  }
  .mini-social-mailru{
    background-image: url("/img/mini-social-mailru.png");
  }
}
