.footer{
	width: 100%;
	float: left;
	display: block;
	border-top: 1px solid #DEDEDE;
}
.footer__left{
	width: 20%;
	float: left;
	display: block;
	position: relative;
	p{
		font-family: $or;
		font-size: 11px;
		color: #848484;
		text-align: left;
		padding: 10px 0;
		margin: 0;
		width: 90%;
	}
}
.footer__right{
	width: 80%;
	float: left;
	display: block;
}
.logo-icon{
	display: block;
	position: relative;
	width: 80px;
	height: 20px;
	margin-top: 80px;
	padding-bottom: 10px;
	padding-left: 10px;
	img{
		width: 100%;
		height: auto;
		position: absolute;
	    top: 50%;
	    left: 50%;
	    -webkit-transform: translate(-50%, -50%);
	       -moz-transform: translate(-50%, -50%);
	        -ms-transform: translate(-50%, -50%);
	         -o-transform: translate(-50%, -50%);
	            transform: translate(-50%, -50%);
	}
}
.column-footer{
	width: 20%;
	display: block;
	float: left;
	padding-top: 40px;
	padding-left: 10px;
	li{
		a{
			color: #1a97bb;
			font-size: 12px;
			font-family: $or;
		}
	}
}
.mob-version{
	width: 15%;
	float: right;
	display: block;
	margin-top: 60px;
	a{
		color: #1a97bb;
		width: 100%;
		font-family: $ob;
		font-size: 14px;
	}
}
.mob-icon{
	width: 30px;
	height: 50px;
	display: block;
	position: relative;
	float: left;
	margin-right: 10px;
	img{
		width: 100%;
		height: auto;
		position: absolute;
	    top: 50%;
	    left: 50%;

	    -webkit-transform: translate(-50%, -50%);
	       -moz-transform: translate(-50%, -50%);
	        -ms-transform: translate(-50%, -50%);
	         -o-transform: translate(-50%, -50%);
	            transform: translate(-50%, -50%);
	}
}
.modalas{
	width: 50%;
	float: left;
	display: none;
	/* background-color: #FFF; */
	background-color: #f5f5f5;
	z-index: 30;
	position: fixed;
    top: 25%;
    right: 0;
    bottom: 0;
    left: 0;
	border: 5px;
	margin: 0 auto;
	    overflow-y: hidden !important;
	padding: 30px 50px;
	height: 380px;
	-webkit-border-radius: 10px;
	        border-radius: 10px;

}

.modal-header{
	border: none;
	h2{
		text-align: center;
		font-family: $ob;
		font-size: 16px;
		color: #000;
		margin: 0;
	}

}
.krest{
	display: block;
	font-size: 35px;
	font-weight: bold;
	position: absolute;
    float: right;
    right: 30px;
    top: 30px;
    z-index: 10;
    opacity: 1;
}
.modal-item{
	width: 20%;
	display: inline-block;
	text-align: -webkit-center;
	margin: 30px 0;
	cursor: pointer;
	span{
		font-size: 12px;
		font-family: $or;
		color: #000;
		padding-top: 15px;
    	display: block;
	}
}
.thumb{
	display: block;
	position: relative;
	width: 75px;
	height: 55px;
	img{
		max-width: 100%;
		position: absolute;
	    top: 50%;
	    left: 50%;
	    -webkit-transform: translate(-50%, -50%);
	       -moz-transform: translate(-50%, -50%);
	        -ms-transform: translate(-50%, -50%);
	         -o-transform: translate(-50%, -50%);
	            transform: translate(-50%, -50%);
	}
}
.modal-2{
	width: 50%;
	display: inline-block;
	vertical-align: top;
	margin-right: -4px;
	position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
	display: block;
	/* background-color: #FFF; */
	background-color: #f5f5f5;
	z-index: 30;
	border: 5px;
	margin: 0 auto;
	padding: 30px 50px;
	height: 380px;
	-webkit-border-radius: 10px;
	        border-radius: 10px;

}
.column-modal{
	width: 33.3%;
	display: inline-block;
	vertical-align: top;
	margin-right: -4px;

	h3{
		font-size: 15px;
		font-family: $ob;
		text-align: center;
		color: #000;
		padding-bottom: 10px;
		margin-bottom: 0;
		border-bottom: 1px solid #000;
	}
}
.caret {
	width: 0;
    height: 0;
    border-top: 5px solid transparent;
    border-left: 10px solid #f98b10;
    border-bottom: 5px solid transparent;
    margin-left: 10px;
}
.obvertka{
	width: 100%;
	float: left;
	display: block;
	text-align: center;
	border-left: 1px solid #000;
	height: 320px;
	overflow-y: auto;
}
.style-scroll::-webkit-scrollbar-track
{
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
	border-radius: 10px;

	background-color: #F5F5F5;
}
.style-scroll::-webkit-scrollbar
{
	margin-right:10px;
	width: 12px;
	background-color: #F5F5F5;
}

.style-scroll::-webkit-scrollbar-thumb
{
	border-radius: 10px;
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
	background-color: #555;
}
.style-scroll::-webkit-scrollbar-arrow-color{
	color: rgba(0,0,0,0.3);
}

.obvertka-first{
	border-left: none;
}
.heading-change{
		text-align: left;
		display: block;
		border: none;
		border-left: 3px solid transparent;
		font-size: 13px;
		font-family: $or;
		color: #000;
		margin: 10px 0;
		cursor: pointer;
		margin-left: 10px;
		padding-left: 10px;
		&:hover{
		border-left: 3px solid #e6e814;
		-webkit-box-sizing: border-box;
		   -moz-box-sizing: border-box;
		        box-sizing: border-box;
		}
}
.heading-change.active{
	border-left: 3px solid #e6e814;
	-webkit-box-sizing: border-box;
		 -moz-box-sizing: border-box;
					box-sizing: border-box;
}
