.kabinet-active-ad{
  width: 100%;
  float: left;
  position: relative;
}

.kabinet-active-ad__additionaly-option{
  width: 100%;
  float: left;
  position: relative;
  padding: 15px 0;
  box-sizing: border-box;
  ul{
    width: 40%;
    display: inline-block;
    float: left;
    margin: 0;
    padding: 0;
    .option{
      display: inline-block;
      list-style-type: none;
      margin: 0 15px;
      &:first-of-type{
        margin-left: 0;
      }
      a{
        font-family: $ob;
        font-size: 12px;
        color: #6f610e;
        border-bottom: 1px dashed #afdbee;
        text-decoration: none;
        box-sizing: border-box;
      }
    }
    .option.active{
      a{
        color: #000;
        border-bottom: none;
      }
    }
  }
  .option-2{
    font-size: 12px;
    font-family: $ob;
    color: #334a51;
    background-color: #ffd800;
    border: 1px solid #dedede;
    border-radius: 12px;
    text-decoration: none;
    padding: 5px 10px;
    box-sizing: border-box;
    margin: 0 5px;
  }
  .up-option{
    display: inline-block;
    size: 17px 15px;
    background-image: url("/img/up-option.png");
    position: relative;
    top: 2px;
    margin-right: 5px;
  }
}

.kabinet-active-ad__content{
  width: 100%;
  float: left;
  position: relative;
  .item{
    width: 100%;
    float: left;
    position: relative;
    .kabinet-active-ad__content_ad{
      width: 50%;
      float: left;
      display: inline-block;
    }
    .item-edit-ad{
      width: 15%;
      float: left;
      position: relative;
      text-align: center;
      margin-top: 50px;
      .edit{
        display: inline-block;
        min-width: 50%;
        border: 1px solid #dedede;
        border-radius: 12px;
        padding: 5px ;
        box-sizing: border-box;
        text-decoration: none;
        color: #6f610e;
        font-size: 12px;
        font-family: $ob;
        .edit-icon{
          background-image: url("/img/edit-icon.png");
          size: 10px 15px;
          display: inline-block;
          background-repeat: no-repeat;
          background-size: cover;
          margin-right: 5px;
          margin-left: 2px;
          float: left;
          position: relative;
          top: 3px;
        }
      }
    }
  }
}

.checkbox, .checkbox0{
  width: 10%;
  display: inline-block;
  float: right;
  text-align: right;
  position: relative;
  margin-top: 52px;
  input[type=checkbox] {
    display: none;
  }
  label{
    size: 20px 22px;
    background-image: url("/img/checkbox-sprite.png");
    background-position: 0px 0px;
  }
  input[type=checkbox]:checked + label{
    background-image: url('/img/checkbox-sprite.png');
    background-repeat: no-repeat;
    background-position: 0px -27px;
    display: block;
    size: 21px;
    text-align: right;
    float: right;
  }
}
.checkbox0{
  margin-top: 0;
}
