.bar-con{
  background-color:#ccc;
  height:0.5em;
  float: left;
  display: inline-block;
  width: 15%;
  border-radius: 50px;
  position: relative;
  margin-top: 70px;
  span{
    padding-top: 10px;
    display: inline-block;
    text-align: left;
    left: 2px;
    position: relative;
    font-size: 12px;
    font-family: $ob;
    color: #878787;
  }
}
.bar{
  float:left;
  height:100%;
  width:0%;
  border-top-left-radius: 50px;
  border-bottom-left-radius: 50px;

}
.bar-one{
  .bar{
    background-color: #ffdf2d;
  }

  // .transition;
}
