.header__top{
	width: 100%;
	float: left;
	display: block;
	padding: 20px 0;
	border-bottom: 1px solid #DEDEDE;
}
.line{
	&:hover{
		border-bottom: 1px solid #1a97bb;
	}
}
.header__top_left, .header__top_right{
	float: left;
	display: block;
	width: 50%;
}
.header__top_logo{
	width: 20%;
	float: left;
}
.header__top_count-ad{
	width: 80%;
	float: left;
	text-align: left;
	p{
		font-size: 14px;
		margin: 0;
		font-family: $or;
		color: #000;
		a{
			text-decoration: none;
			text-transform: uppercase;
			font-family: $oeb;
			color: #bca008;
			border-bottom: 1px solid #1a97bb;
			cursor: pointer;
		}
	}
}
 .header__top_advert, .header__top_price{
	width: 25%;
	float: left;
	color: #000;
	text-decoration: none;
	font-size: 14px;
	font-family: $or;
	cursor: pointer;
}
.header__top_favorites{
	width: 20%;
	color: #000;
	float: left;
	text-decoration: none;
	cursor: pointer;
	font-size: 14px;
	font-family: $or;
}
.header__top_price{
	padding-left: 10px;
	width: 20%;
	font-weight: 900;
}
.circle{
	position: absolute;
	width: 13px;
	height: 12px;
	background-color: #f78900;
	border-radius: 6px;
	float: right;
	display: inline-block;
	right: -4px;
    top: -3px;
    font-size: 11px;
    color: #FFF;
    font-family: $ob;
    text-align: center;
    line-height: 12px;

}
.header_top_icon{
	height: 18px;
	width: 18px;
	background-image: url('../img/sprite.png');
	background-repeat: no-repeat;
	float: left;
	margin-right: 10px;
	position: relative;
}
.msg-icon{
	background-image: url("../img/msg.png");
}
.exit-icon{
	background-image: url("../img/exit.png");
}
.favorites_icon{
	background-image: url('../img/sprite.png');
	background-position: 0px 0px;
	width: 18px;
	height: 18px;
	cursor: pointer;
}
.favorites_icon_press{
	background-image: url('../img/sprite.png');
	background-position: -18px 0px;
	width: 18px;
	height: 18px;
	cursor: pointer;
}
.advert_icon{
	background-image: url('../img/sprite.png');
	background-position: 0px -18px;
	 width: 17px;
  height: 15px;
	cursor: pointer;
}
.advert_icon_press{
	background-image: url('../img/sprite.png');
	background-position: -17px -18px;
	 width: 17px;
  height: 15px;
	cursor: pointer;
}
.price_icon{
	width: 22px;
	height: 19px;
	background-image: url('../img/price.png');
	float: left;
	background-repeat: no-repeat;
	cursor: pointer;
	margin-right: 10px;
}
.header__top_user{
	width: 30%;
	float: right;
	position: relative;
	&:hover{
		.private-cabinet{
			display: block;
		}
	}
}
.header_user{
	width: 100%;
	float: right;


}

.user-pic{
	width: 31px;
	height: 31px;
	float: left;
	position: relative;
	margin-right: 10px;
	bottom: 5px;
	img{
		position: absolute;
	    top: 50%;
	    left: 50%;
	    -webkit-transform: translate(-50%, -50%);
	       -moz-transform: translate(-50%, -50%);
	        -ms-transform: translate(-50%, -50%);
	         -o-transform: translate(-50%, -50%);
	            transform: translate(-50%, -50%);
	}
}
.private-cabinet{
	display: none;
	position: absolute;
	z-index: 10;
	width: 70%; //править ширину!!!
	text-align: center;
	bottom: -52px;
	padding-top: 10px;
	padding-bottom: 5px;
	padding-left: 5px;
	padding-right: 5px;
	box-sizing: border-box;
	background-color:rgba(145, 190, 206, 0.51);
		a{
			margin: 5px 0;
			text-align: left;
			width: 100%;
			float: left;
			margin: 0 auto;
			color: #000;
			font-family: $or;
			font-size: 14px;
		}
}
.user-name-link{
	min-width: 40%;
	display: inline-block;
	border-bottom: 1px solid #1a97bb;
	text-decoration: none;
	&:hover{
		text-decoration: none;
	}
}
.user-name{
	min-width: 40%;
	display: inline-block;
	float: left;
	font-size: 16px;
	font-family: $ob;
	color: #000;
	position: relative;
}

.rubl-icon{
	position: relative;
	display: inline-block;
	height: 16px;
	width: 16px;
	svg{
		display: inline-block;
		height: 16px;
		width: 16px;
		vertical-align: middle;
	}
}

.header__middle{
	width: 100%;
	float: left;
	padding: 20px 0;
}
.header__middle_left{
	width: 50%;
	float: left;
	strong{
		font-family: $osb;
		font-size: 14px;
	}
}
.header__middle_right{
	width: 50%;
	float: left;
}
.dobavit{
	display: block;
	width: 35%;
	float: right;
	margin-left: 15px;
	a{
		float: left;
		text-decoration: none;
		color: #000;
		width: 90%;
		line-height: 18px;
		font-size: 16px;
		font-family: $ob;
		padding-left: 10px;
		position: relative;
	}
}
.icon-plus{
	float: left;
	display: block;
	height: 16px;
	width: 10%;
	background-image: url('../img/icon_plus.png');
	background-repeat: no-repeat;
}

.home-baner{
	height: 150px;
	width: 100%;
	float: left;
	display: block;
	position: relative;
	text-align: center;
	padding: 30px 0;
}
.baner{
	height: 90px;
	background-color: #f1f1f1;
	width: 50%;
	margin: 0 auto;
}
.header__bottom-home{
	width: 100%;
	float: left;
	display: inline-block;
	margin-bottom: 20px;
}
.header__bottom-home-left{
	float: left;
	display: block;
	width: 20%;
	.category-item{
		margin: 0;
		border: 2px solid #000;
		height: 34px;
		border-radius: 13px;
	}
}
.header__bottom-home-right{
	float: left;
	width: 80%;
	display: block;

}
.input-search{
	width: 85%;
	float: left;
	display: inline-block;
	height: 34px;
	line-height: 34px;
	background-color: #fff;
	border: 2px solid #ffd800;
	border-radius: 15px;
	padding-left: 20px;
	z-index: 1;
	position: relative;
	left: 22px;
	outline: none;
}
.button-search{
	width: 15%;
	float: left;
	display: inline-block;
	height: 34px;
	background-color: #ffd800;
	border-radius: 15px;
	font-size: 16px;
	font-family: $osb;
	text-align: center;
	line-height: 35px;
	color: #000;
}


.header__middle-home{
	width: 100%;
	float: left;
	display: block;
	background-color: #fff6c4;
	height: 50px;
}
.header__middle-home-left{
	float: left;
	width: 50%;
	display: inline-block;
	padding: 15px 0;
}
.dobavit-home{
	display: block;
	width: 35%;
	float: left;
	margin-right: 15px;
	a{
		float: left;
		text-decoration: none;
		color: #000;
		width: 90%;
		line-height: 18px;
		font-size: 16px;
		font-family: $ob;
		padding-left: 10px;
		position: relative;
	}
}
.header__middle-home-right{
	float: left;
	width: 50%;
	display: inline-block;
}
.home-menu{
	width: 100%;
	float: right;
	display: inline-block;
	list-style-type: none;
	padding-left: 50px;
	margin: 0;
	padding-top: 15px;
	padding-bottom: 15px;
	li{
		display: inline-block;
		float: left;
		margin-left: 40px;
		a{
			font-size: 14px;
			color: #01080a;
			font-family: $ob;
		}

	}
}

.category{
	width: 100%;
	float: left;
	position: relative;
	z-index: 10;
}

.delivery_block1{
width: 100%;
padding:  0;
font-size: 11px;
display: block;
float: left;
position: relative;
border-radius: 13px;

}
.delivery_block1 .delivery_list1 {
padding: 0px 6px 3px 6px;
height:35px;
cursor: pointer;
margin: 0 20px 0 0;
color: #ed4226;
width: 100%;
z-index: 11;
border-radius: 13px;
border: 1px solid #000;

}
.delivery_list1 #btn {
		width: 0;
		height: 0;
  	border-left: 7px solid transparent;
  	border-right: 7px solid transparent;
  	border-top: 12px solid #ffd800;
		float: right;
		position: absolute;
		right: 5%;
		top: 10px;


}
.delivery_list1 span {
padding: 2px 0 0 30px;
display: inline-block;
position: absolute;
font-weight: bold;
font-size: 14px;
font-family: $osb;
color: #000;
height: 35px;
line-height: 30px;
}
.delivery_block1 ul.cities_list1 {
margin-top: 0px;
z-index: 10;
background-color: rgba(212, 211, 211, 0.95);
position: absolute;
left: 7%;
width: 85%;
padding: 0;
margin: 0 auto;
border-bottom-left-radius: 15px;
border-bottom-right-radius: 15px;
display: none;
}
ul.cities_list1 li {
padding: 6px 0px 6px 30px;
list-style-type: none;
font-family: $or;
font-size: 16px;
color: #000;
position: relative;
&:before{
	content: "";
	position: absolute;
	background-image: url("../img/disk.png");
	width: 5px;
	height: 5px;
	margin-left: -20px;
  margin-top: 10px;

}

}
ul.cities_list1 li:hover {
cursor: pointer;
background: #fff;
}
.delivery_block1 .delivery_text1 {
padding: 3px 5px;
margin: 5px 0 0 3px;
}
