.activation{
  width: 100%;
  float: left;
  display: block;
  position: relative;
  text-align: center;
  .reg-form{
    margin: 0 auto;
    float: none;
    .row-knopka{
      .reg-form-send{
        min-width: 30%;
        text-transform: lowercase;
        &:after{
          width: 50%;
        }
      }
    }
  }

}
.activation-title{
  font-size: 18px;
  font-family: $or;
  border: 7px solid #ffd800;
  width: 50%;
  padding: 35px 20px;
  box-sizing: border-box;
  margin: 0 auto 10px;
  float: none;
}
.activatioN-info{
  font-family: $opl;
  font-size: 12px;
  color: #424242;
}
.goback{
  width: 100%;
  display: inline-block;
  font-size: 14px;
  font-family: $or;
  padding: 10px 0 40px;
  box-sizing: border-box;
  font-weight: 600;
}
.goback-link{
  color: #a68e0c;
  text-decoration: underline;
}
.goback-1{
  min-width: 17%;
  display: inline-block;
  font-size: 14px;
  font-family: $or;
  padding: 10px 0 40px;
  box-sizing: border-box;
  font-weight: 600;
}
