.header__top{
	width: 100%;
	float: left;
	display: block;
	padding: 20px 0;
	border-bottom: 1px solid #DEDEDE;
}
.line{
	&:hover{
		border-bottom: 1px solid #1a97bb;
	}
}
.header__top_left, .header__top_user-vhod{
	float: left;
	display: block;
	width: 50%;
}
.header__top_logo{
	width: 20%;
	float: left;
}
.header__top_count-ad{
	width: 80%;
	float: left;
	text-align: left;
	p{
		font-size: 14px;
		margin: 0;
		font-family: $or;
		color: #000;
		a{
			text-decoration: none;
			text-transform: uppercase;
			font-family: $oeb;
			color: #bca008;
			border-bottom: 1px solid #1a97bb;
			cursor: pointer;
		}
	}
}

.header__login, .header__regist{
  width: 25%;
  display: inline-block;
  float: right;
  font-size: 16px;
  font-family: $ob;
  color: #a68e0c;
  line-height: 16px;
  cursor: pointer;
  text-decoration: none;
  &:hover{
    color: #1a97bb;
  }
}
.header__login{
  width: 15%;
}
.header_top_icon{
	height: 18px;
	width: 18px;
	background-repeat: no-repeat;
	float: left;
	margin-right: 10px;
	position: relative;

}
.regist-user{
	background-image: url('../img/regist-user.png');
  background-position: 0px 0px;
	width: 18px;
	height: 18px;
	cursor: pointer;
}
.login-header{
	background-image: url('../img/pic-login.png');
background-position: 0px 0px;
	width: 18px;
	height: 18px;
	cursor: pointer;
}
